<template>
	<div class="row">
        <div class="loading" v-if="load">
            <div class="load"><h4>TE</h4><h4>Phim</h4></div>
        </div>
        <div class="box">
            <Item
                v-for="(item, i) in list"
                :key="i"
                :item="item"
            />
        </div>
    </div>
    <div class="row">
        <div class="num-page" v-if="!load">
            <ul>
                <li @click="() => this.$router.push({query: {page: 1}})">1</li>
                <li><i class="fa-solid fa-circle-arrow-left" @click="changePage(-1)"></i></li>
                <li>{{num_page}}</li>
                <li><i class="fa-solid fa-circle-arrow-right" @click="changePage(1)"></i></li>
                <li @click="() => this.$router.push({query: {page: page}})">{{page}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
import Item from './item'

export default {
	name: 'List',
    props: ['list', 'page'],
    data(){
        return {
            load: true,
            num_page: 1
        }
    },
    methods: {
        changePage(num){
            if(num < 0 && this.num_page > 1){
                this.num_page--
            } else if(num > 0 && this.num_page < this.page){
                this.num_page ++
            }
            this.$router.push({query: {page: this.num_page}})
        }
    },
    components: {
        Item
    },
    watch: {
        list: function(){
            this.load = false
            if(this.$route.query.page){
                this.num_page = Number(this.$route.query.page)
            }
        },
        '$route.query': function(){
            if(this.$route.query.page){
                this.num_page = Number(this.$route.query.page)
            }
        },
        '$route.params': function(){
            this.num_page = 1
            setTimeout(() => {
                this.load = true
            })
        }
    }
}
</script>

