<template>
	<div class="main" v-if="reset">
		<Nav/>
		<Movie :movie="movie"/>
        <Play :movie="movie"/>
        <SuggestCategory
            v-for="(item, i) in movie.category"
            :key="i"
            :item="item"
        />
		<Footer/>
	</div>
</template>

<script>
import axios from 'axios'
import Nav from '../components/nav'
import Movie from '../components/play'
import SuggestCategory from '../components/play/suggest-categiry'
import Footer from '../components/footer'
import Play from '../components/play/play'

export default {
	name: 'MainPlay',
    data(){
        return {
            movie: new Object(),
            reset: true,
        }
    },
    async created(){
        window.scrollTo(0,0)
        this.getMovie()
    },
    methods: {
        async getMovie(){
            try {
                const result = await axios.get('/api/phim/' + this.$route.params.slug)
                if(result.data.success){
                    this.movie = result.data.data
                    this.movie.episodes = Object.entries(result.data.data.episodes[0])[0][1]
                    document.title = result.data.data.name
                }
            } catch(err){
                console.log(err)
            }
        },
        async createWatched(){
            try {
                const result = await axios.post('/api/watched', {email: this.$store.state.user.email, slug: this.$route.params.slug})
                if(!result.data.success){
                    this.createWatched()
                }
            } catch(err){
                console.log(err)
            }
        }
    },
    mounted(){
        if(this.$store.state.auth){
            this.createWatched()
        }
    },
	beforeMount() {
		document.title = 'TE Phim | Xem phim mới | Phim online'
	},
    watch: {
        '$route.params': function(){
            this.reset = false
            this.getMovie()
            window.scrollTo(0,0)
            setTimeout(() => {
                this.reset = true
            }, 100)
        }
    },
	components: {
		Nav,
        Movie,
        SuggestCategory,
        Play,
		Footer
	}
}
</script>

<style scoped>

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

</style>
