<template>
	<div class="main">
		<Nav/>
		<div class="container">
			<form @submit.prevent="Login">
                <h2>Đăng nhập</h2>
                <div class="input-group">
                    <label>Email</label>
                    <input type="text" placeholder="Email" v-model="email">
                </div>
                <div class="input-group">
                    <label>Mật khẩu</label>
                    <div class="input">
                        <i class="fa-solid fa-eye" @click="showPass(true)" v-if="!is_show_pass"></i>
                        <i class="fa-solid fa-eye-slash" @click="showPass(false)" v-else="!is_show_pass"></i>
                        <input type="password" placeholder="Mật khẩu" ref="pass" v-model="password">
                    </div>
                </div>
                <div class="message-err" v-if="message">
                    <h3>{{message}}</h3>
                </div>
                <button type="submit" v-if="!loading">Đăng nhập</button>
                <div class="loading" v-if="loading"></div>
            </form>
		</div>
	</div>
	<Footer/>
</template>

<script>
import Nav from '../components/nav'
import Footer from '../components/footer'
import axios from 'axios'

export default {
	name: 'MainLogin',
    data(){
        return {
            email: '',
            password: '',
            agree: false,
            message: '',
            loading: false,
            is_show_pass: false
        }
    },
    methods: {
        async Login(){
            const re = /\S+@\S+\.\S+/
            if(this.email){
                if(this.password){
                    if(re.test(this.email)){
                        if(this.password.length >= 6){
                            if(!this.loading){
                                this.loading = true
                                const response = await axios.post('/api/login', {
                                                                    email: this.email,
                                                                    password: this.password
                                                                })
                                if(response.data.success){
                                    localStorage.setItem('token', response.data.token)
                                    this.$router.push({name: 'Home'})
                                } else {
                                    this.message = response.data.message
                                }
                                this.loading = false
                            }
                        } else {
                        this.message = 'Tài khoản không chính xác'
                        }
                    } else {
                    this.message = 'Tài khoản không chính xác'
                    }
                } else {
                    this.message = 'Vui lòng nhập mật khẩu'
                }
            } else {
                this.message = 'Vui lòng nhập email'
            }
            if(this.message){
                setTimeout(() => {
                    this.message = ''
                }, 3000)
            }
        },
        showPass(test){
            if(test){
                this.$refs.pass.type = 'text'
                this.is_show_pass = true
            } else {
                this.$refs.pass.type = 'password'
                this.is_show_pass = false
            }
        }
    },
	beforeMount() {
        window.scrollTo(0,0)
		document.title = 'Đăng ký'
	},
	components: {
		Nav,
		Footer
	}
}
</script>

