<template>
	<div class="main">
		<Nav/>
        <List :list="list" :page="page" :key="num_page"/>
		<Footer/>
	</div>
</template>

<script>
import axios from 'axios'
import Nav from '../components/nav'
import List from '../components/list'
import Footer from '../components/footer'
import slug from 'slug'

export default {
	name: 'MainCategory',
    data(){
        return {
            list: [],
            page: 0,
            num_page: 1,
            category: ['Hành Động',
                        'Tình Cảm',
                        'Hài Hước',
                        'Cổ Trang',
                        'Tâm Lý',
                        'Hình Sự',
                        'Chiến Tranh',
                        'Thể Thao',
                        'Võ Thuật',
                        'Viễn Tưởng',
                        'Phiêu Lưu',
                        'Khoa Học',
                        'Kinh Dị',
                        'Âm Nhạc',
                        'Thần Thoại',
                        'Tài Liệu',
                        'Gia Đình',
                        'Chính kịch',
                        'Bí ẩn',
                        'Học Đường',
                        'Kinh Điển'],
        }
    },
    created(){
        window.scrollTo(0,0)
        if(this.$route.query.page){
            this.num_page = Number(this.$route.query.page)
        }
        this.getList()
    },
    methods: {
        async getList(){
            try {
                const text = slug(this.$route.params.search)
                const result = await axios.get('/api/tim-kiem/' + text, {params: {page: Number(this.num_page) - 1}})
                if(result.data.success){
                    this.list = result.data.data
                    this.page = Math.floor(result.data.count / 20)
                    if(result.data.count % 20 != 0){
                        this.page++
                    }
                }
            } catch(err){
                console.log(err)
            }
        }
    },
	beforeMount() {
		document.title = 'Tìm kiếm ' + this.$route.params.search
	},
    watch: {
        '$route.query': function(){
            if(this.$route.query.page){
                this.num_page = Number(this.$route.query.page)
                this.getList()
                window.scrollTo(0,0)
                this.list = []
            }
        },
        '$route.params': function(){
            if(this.$route.params.search){
                this.getList()
                window.scrollTo(0,0)
                this.list = []
                this.num_page = 1
                document.title = 'Tìm kiếm ' + this.$route.params.search
            }
        }
    },
	components: {
		Nav,
        List,
		Footer
	}
}
</script>

<style scoped>

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

</style>
