<template>
	<div class="row">
        <div class="video">
            <div class="loading" v-if="load_video">
                <h3>Đang tải </h3>
                <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
            </div>
            <div class="iframe" v-show="!load_video">
                <iframe ref="iframe" :src="movie.episodes.server_data[episode-1].link_embed" frameborder="0" v-if="load" allow="fullscreen;"></iframe>
            </div>
        </div>
        <div class="eposide" v-if="load && movie.episodes.server_data.length > 1">
            <ul>
                <li><h4>Tập: </h4></li>
                <li
                    v-for="(item, i) in movie.episodes.server_data"
                    :key="i"
                    :class="{li: (i + 1)=== episode}"
                    @click="selectEpisode(i)"
                >{{item.name}}</li>
            </ul>
        </div>
    </div>
    <div class="content" v-html="movie.content"></div>
</template>

<script>

export default {
	name: 'Play',
    props: ['movie'],
    data(){
        return {
            episode: 1,
            load: false,
            load_video: true
        }
    },
    created() {
        if(this.$route.query.tap){
            this.episode = Number(this.$route.query.tap)
        }
    },
    methods: {
        selectEpisode(i){
            if(i + 1 !== this.episode){
                this.$router.push({query: {tap: i + 1}})
            }
        },
        onLoadIframe(){
            console.log('load success')
        }
    },
    watch: {
        movie: function(){
            this.load = true
            setTimeout(() => {
                this.$refs.iframe.onload = () => {
                    this.load_video = false
                }
            },100)
        }
    }
}
</script>

