<template>
	<div class="box">
        <form @submit.prevent="Save">
            <div class="btn">
                <i class="fa-solid fa-arrow-left-long" @click="() => this.$parent.$data.show_edit = false"></i>
                <div class="loading" v-if="load"></div>
                <button type="submit" v-else="load">Lưu</button>
            </div>
            <div class="avata">
                <div class="input-file">
                    <i class="fa-solid fa-camera"></i>
                    <div class="input-img">
                        <input type="file" @change="selectImage" accept="image/*" />
                    </div>
                </div>
                <img :src="img_avata" >
            </div>
            <div class="input-group">
                <label>{{name.length}}/50</label>
                <input type="text" v-model="name">
            </div>
        </form>
    </div>
    <EditAvata :image="image" :destroyEditAvata="destroyEditAvata" :saveChangeEditAvata="saveChangeEditAvata" v-if="show_edit_avata" />
</template>

<script>
import axios from 'axios'
import EditAvata from './edit-avata'

export default {
	name: 'Edit',
    data(){
        return {
            img_avata: '',
            name: '',
            load: false,
            image: '',
            show_edit_avata: false
        }
    },
    methods: {
        async Save(){
            var obj = new Object()
            if(this.name !== this.$store.state.user.name && this.name.length < 50 && this.name.length > 0){
                obj.token = localStorage.getItem('token')
                obj.name = this.name
            }
            if(this.img_avata !== this.$store.state.user.avata.url){
                obj.token = localStorage.getItem('token')
                obj.image = this.img_avata
            }
            if(obj.token){
                this.load = true
                try {
                    const token = localStorage.getItem('token')
                    const result = await axios.put('/api/profile', obj)
                    if(result.data.success){
                        if(obj.name){
                            this.$store.dispatch('updateName', this.name)
                        }
                        if(obj.image){
                            this.$store.dispatch('updateAvata', this.img_avata)
                        }
                        this.$parent.$data.show_edit = false
                    }
                } catch(err){
                    console.log(err)
                }
                this.load = false
            }
        },
        selectImage(e){
            const render = new FileReader()
            render.readAsDataURL(e.target.files[0])
            render.onloadend = () => {
                this.image = render.result
                this.show_edit_avata = true
            }
        },
        destroyEditAvata(){
            this.show_edit_avata = false
            this.image = ''
        },
        saveChangeEditAvata(data){
            this.img_avata = data
            this.destroyEditAvata()
        }
    },
    created(){
        this.img_avata = this.$store.state.user.avata.url
        this.name = this.$store.state.user.name
    },
    components: {
        EditAvata
    }
}
</script>

