<template>
	<div class="main">
		<Nav/>
        <List :list="list" :page="page" :key="num_page"/>
		<Footer/>
	</div>
</template>

<script>
import axios from 'axios'
import Nav from '../components/nav'
import List from '../components/list'
import Footer from '../components/footer'

export default {
	name: 'MainList',
    data(){
        return {
            list: [],
            page: 0,
            num_page: 1
        }
    },
    created(){
        window.scrollTo(0,0)
        if(this.$route.query.page){
            this.num_page = Number(this.$route.query.page)
        }
        this.getList()
    },
    methods: {
        async getList(){
            try {
                const  type = this.$route.params.type
                const theloai = type === 'phim-bo' ? 'series':type === 'phim-le' ? 'single':type === 'shows' ? 'tvshows':'hoathinh'
                const result = await axios.get('/api/danh-sach/' + theloai, {params: {page: Number(this.num_page) - 1}})
                if(result.data.success){
                    this.list = result.data.data
                    this.page = Math.floor(result.data.count / 20)
                    if(result.data.count % 20 != 0){
                        this.page++
                    }
                }
            } catch(err){
                console.log(err)
            }
        }
    },
	beforeMount() {
        const  type = this.$route.params.type
        const theloai = type === 'phim-bo' ? 'Phim bộ':type === 'phim-le' ? 'Phim lẻ':type === 'shows' ? 'Show':'Hoạt hình'
		document.title = theloai
	},
    watch: {
        '$route.query': function(){
            if(this.$route.query.page){
                this.num_page = Number(this.$route.query.page)
                this.getList()
                window.scrollTo(0,0)
                this.list = []
            }
        },
        '$route.params': function(){
            if(this.$route.params.type){
                this.getList()
                window.scrollTo(0,0)
                this.list = []
                this.num_page = 1
                const  type = this.$route.params.type
                const theloai = type === 'phim-bo' ? 'Phim bộ':type === 'phim-le' ? 'Phim lẻ':type === 'shows' ? 'Show':'Hoạt hình'
                document.title = theloai
            }
        }
    },
	components: {
		Nav,
        List,
		Footer
	}
}
</script>

<style scoped>

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

</style>
