<template>
	<div class="box-edit">
        <div class="container-edit">
            <div class="btn">
                <i class="fa-solid fa-arrow-left-long" @click="destroyEditAvata"></i>
                <button @click="Save()">Lưu</button>
            </div>
            <img :src="image" ref="imageedit" alt="" class="img-edit">
        </div>
    </div>
</template>

<script>
import croppie from 'croppie'

export default {
	name: 'EditAvata',
    props: ['image', 'destroyEditAvata', 'saveChangeEditAvata'],
    data(){
        return {
            crop: null
        }
    },
    methods: {
        async Save(){
            const data = await this.crop.result('base64')
            this.saveChangeEditAvata(data)
        }
    },
    beforeDestroy() {
        this.crop.destroy()
    },
    mounted(){
        this.crop = new croppie(this.$refs.imageedit, {
            viewport: { width: 300, height: 300 }
        })
    }
}
</script>

