<template>
	<div class="main" v-if="reset">
		<Nav/>
		<Movie :movie="movie"/>
        <SuggestCategory
            v-for="(item, i) in movie.category"
            :key="i"
            :item="item"
        />
	</div>
    <Footer/>
</template>

<script>
import axios from 'axios'
import Nav from '../components/nav'
import Movie from '../components/movie'
import SuggestCategory from '../components/movie/suggest-categiry'
import Footer from '../components/footer'

export default {
	name: 'MainMovie',
    data(){
        return {
            movie: new Object(),
            reset: true
        }
    },
    created(){
        window.scrollTo(0,0)
        this.getMovie()
    },
    methods: {
        async getMovie(){
            try {
                const result = await axios.get('/api/phim/' + this.$route.params.slug)
                if(result.data.success){
                    this.movie = result.data.data
                    document.title = result.data.data.name
                }
            } catch(err){
                console.log(err)
            }
        }
    },
	beforeMount() {
		document.title = 'TE Phim | Xem phim mới | Phim online'
	},
    watch: {
        '$route.params': function(){
            this.reset = false
            this.getMovie()
            window.scrollTo(0,0)
            setTimeout(() => {
                this.reset = true
            }, 100)
        }
    },
	components: {
		Nav,
        Movie,
        SuggestCategory,
		Footer
	}
}
</script>

<style scoped>

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

</style>
