<template>
	<div class="row">
        <div class="box">
            <h2>Gợi ý phim {{item.name.toLowerCase()}}</h2>
            <div class="box-suggest">
                <div class="chevron-left" @click="onScrollX(30)"><i class="fa-solid fa-circle-chevron-left"></i></div>
                <div class="chevron-right" @click="onScrollX(-30)"><i class="fa-solid fa-circle-chevron-right"></i></div>
                <div class="box-suggest-child" @wheel.prevent="handleWheel" ref="suggest">
                    <div class="suggest">
                        <Item
                            v-for="(item, i) in list"
                            :key="i"
                            :item="item"
                        />
                    </div>
                </div>
            </div>
        </div>
    </div>
    <br>
    <br>
</template>

<script>
import axios from 'axios'
import Item from './item'

export default {
	name: 'SuggestCategory',
    props: ['item'],
    data(){
        return {
            list: [],
            left: 0
        }
    },
    created(){
        this.getSuggestCategory()
    },
    methods: {
        async getSuggestCategory(){
            try {
                const result = await axios.get('/api/suggest/category/' + this.item.name)
                if(result.data.success){
                    this.list = result.data.data
                }
            } catch(err){
                console.log(err)
            }
        },
        handleWheel(e){
            if(e.deltaY > 0){
                this.$refs.suggest.scrollLeft += 20
            } else {
                this.$refs.suggest.scrollLeft -= 20
            }
        },
        onScrollX(num){
            this.$refs.suggest.scrollLeft -= num
        }
    },
    components: {
        Item
    }
}
</script>

<style scoped>

.row {
    width: 100%;
    display: flex;
    justify-content: center;
}

.box {
    max-width: 1000px;
    width: 100%;
}

.box-suggest {
    position: relative;
}

.chevron-left {
    position: absolute;
    height: 100%;
    display: flex;
    left: 0;
    align-items: center;
    font-size: 1.5em;
}

.chevron-right {
    position: absolute;
    height: 100%;
    display: flex;
    right: 0;
    align-items: center;
    font-size: 1.5em;
}

.box-suggest i {
    cursor: pointer;
    color: #5d8092;
}

.suggest {
    height: 100%;
    display: grid;
    grid-template-rows: 1fr;
    grid-gap: 1em;
    grid-template-columns: repeat(10, 1fr);
    width: 150%;
}

.box-suggest-child {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: scroll;
    overflow-y: hidden;
}

/* width */
.box-suggest-child::-webkit-scrollbar {
  height: 8px;
}

/* Track */
.box-suggest-child :-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
.box-suggest-child::-webkit-scrollbar-thumb {
  background: #234254; 
}

/* Handle on hover */
.box-suggest-child::-webkit-scrollbar-thumb:hover {
  background: #1e3c4d; 
}

.box h2 {
    color: #5889a5;
    margin-bottom: 0.5em;
}

@media only screen and (max-width: 1100px) {
    .row {
        padding: 0 10%;
        width: 80%;
    }
}

</style>
