<template>
    <div class="loading" v-if="load">
        <div class="load"><h4>TE</h4><h4>Phim</h4></div>
    </div>
	<div class="row" v-show="!load">
        <div class="bg-big">
            <img :src="movie.thumb_url" class="img-big">
        </div>
        <div class="bg-img-gradient"></div>
         <div class="box">
            <img :src="movie.thumb_url" alt="" class="img-left">
            <div class="box-right">
                <h1>{{movie.name}}</h1>
                <div class="btn-play" @click="() => this.$router.push({name: 'Play', params: { slug: movie.slug }})">
                    <i class="fa-solid fa-play"></i>
                    <p>Xem phim</p>
                </div>
                <p v-if="movie.time">Thời lượng: {{movie.time}}</p>
                <p v-if="movie.episode_total">Số tập: {{movie.episode_total}}</p>
                <p v-if="movie.year">Năm sản xuất: {{movie.year}}</p>
                <p v-if="str_country">Quốc gia: {{str_country}}</p>
                <p v-if="str_category">Thể loại: {{str_category}}</p>
                <p v-if="str_actor">Diễn viên: {{str_actor}}</p>
            </div>
        </div>
    </div>
    <div class="content" v-html="movie.content"></div>
</template>

<script>

export default {
	name: 'Index',
    props: ['movie'],
    data(){
        return {
            str_country: '',
            str_category: '',
            str_actor: '',
            load: true
        }
    },
    watch: {
        'movie': function(){
            this.movie.country.forEach((item, i) => {
                if(i !== 0){
                    this.str_country+= ', '
                }
                this.str_country += item.name
            })
            this.movie.category.forEach((item, i) => {
                if(i !== 0){
                    this.str_category+= ', '
                }
                this.str_category += item.name
            })
            this.movie.actor.forEach((item, i) => {
                if(i !== 0){
                    this.str_actor+= ', '
                }
                this.str_actor += item
            })
            this.load = false
        }
    }
}
</script>

