<template>
	<div class="main">
		<Nav/>
		<div class="container">
            <h3>Hi, cảm ơn bạn đã quan tâm website</h3>
			<form @submit.prevent="Register">
                <h2>Đăng ký</h2>
                <div class="input-group">
                    <label>Tên</label>
                    <div class="input-name">
                        <p>{{name.length}}/50</p>
                        <input type="text" placeholder="Tên" v-model="name">
                    </div>
                </div>
                <div class="input-group">
                    <label>Email</label>
                    <input type="text" placeholder="Email" v-model="email">
                </div>
                <div class="input-group">
                    <label>Mật khẩu</label>
                    <div class="input">
                        <i class="fa-solid fa-eye" @click="showPass(true)" v-if="!is_show_pass"></i>
                        <i class="fa-solid fa-eye-slash" @click="showPass(false)" v-else="!is_show_pass"></i>
                        <input type="password" placeholder="Mật khẩu" ref="pass" v-model="password">
                    </div>
                </div>
                <div class="input-group">
                    <label>Nhập lại mật khẩu</label>
                    <div class="input">
                        <i class="fa-solid fa-eye" @click="showPassConf(true)" v-if="!is_show_pass_conf"></i>
                        <i class="fa-solid fa-eye-slash" @click="showPassConf(false)" v-else="!is_show_pass_conf"></i>
                        <input type="password" placeholder="Mật khẩu" ref="pass_conf" v-model="config_password">
                    </div>
                </div>
                <div class="input-check">
                    <label>Đồng ý với điều khoản</label>
                    <input type="checkbox" v-model="agree">
                </div>
                <div class="message-err" v-if="message">
                    <h3>{{message}}</h3>
                </div>
                <button type="submit" v-if="!loading">Đăng ký</button>
                <div class="loading" v-if="loading"></div>
            </form>
		</div>
	</div>
	<Footer/>
</template>

<script>
import Nav from '../components/nav'
import Footer from '../components/footer'
import axios from 'axios'

export default {
	name: 'MainRegister',
    data(){
        return {
            name: '',
            email: '',
            password: '',
            config_password: '',
            agree: false,
            message: '',
            loading: false,
            is_show_pass: false,
            is_show_pass_conf: false
        }
    },
    methods: {
        async Register(){
            const re = /\S+@\S+\.\S+/
            if(this.name){
                if(this.email){
                    if(re.test(this.email)){
                        if(this.password){
                            if(this.password.length > 5){
                                if(this.password === this.config_password){
                                    if(this.agree){
                                        if(!this.loading && this.name.length <= 50){
                                            this.loading = true
                                            const response = await axios.post('/api/register', {
                                                                                name: this.name,
                                                                                email: this.email,
                                                                                password: this.password
                                                                            })
                                            if(response.data.success){
                                                this.$router.push({name: 'Login'})
                                            } else {
                                                this.message = response.data.message
                                            }
                                            this.loading = false
                                        }
                                    } else {
                                        this.message = 'Vui lòng chọn chấp nhận điều khoản'
                                    }
                                } else {
                                    this.message = 'Nhập lại mật khẩu không khớp'
                                }
                            } else {
                            this.message = 'Mật khẩu phải ít nhất 6 ký tự'
                            }
                        } else {
                            this.message = 'Vui lòng nhập mật khẩu'
                        }
                    } else {
                        this.message = 'Vui lòng nhập đúng email'
                    }
                } else {
                    this.message = 'Vui lòng nhập email'
                }
            } else {
                this.message = 'Vui lòng nhập tên'
            }
            if(this.message){
                setTimeout(() => {
                    this.message = ''
                }, 3000)
            }
        },
        showPass(test){
            if(test){
                this.$refs.pass.type = 'text'
                this.is_show_pass = true
            } else {
                this.$refs.pass.type = 'password'
                this.is_show_pass = false
            }
        },
        showPassConf(test){
            if(test){
                this.$refs.pass_conf.type = 'text'
                this.is_show_pass_conf = true
            } else {
                this.$refs.pass_conf.type = 'password'
                this.is_show_pass_conf = false
            }
        }
    },
	beforeMount() {
        window.scrollTo(0,0)
		document.title = 'Đăng ký'
	},
	components: {
		Nav,
		Footer
	}
}
</script>

