<template>
	<div class="main">
		<Nav/>
		<div class="container">
			<div class="row">
                <img :src="this.$store.state.user.avata.url" class="img-avata">
                <div class="row-right">
                    <h2>{{this.$store.state.user.name}}</h2>
                    <p>{{this.$store.state.user.email}}</p>
                    <p>Joined {{months[this.$store.state.user.time_join.month - 1] + ' '  + this.$store.state.user.time_join.day + ', ' + this.$store.state.user.time_join.year}}</p>
                    <button @click="() => show_edit = true">Chỉnh sửa</button>
                </div>
            </div>
             <div class="loading" v-if="show_history">
                <div class="load"><h4>TE</h4><h4>Phim</h4></div>
            </div>
            <div class="history" v-show="!show_history">
                <h2>Đã xem</h2>
                <div class="ul">
                    <ul>
                        <li
                            v-for="(item, i) in watcheds"
                            :key="i"
                            @click="() => this.$router.push({name: 'Movie', params: { slug: item.slug }})"
                        >
                            <div class="item-history">
                                <img :src="item.movie.thumb_url" alt="">
                                <div class="item-history-detail">
                                    <h3>{{item.movie.name}}</h3>
                                    <div class="time">
                                        <p>{{item.time.h < 10 ? '0' + item.time.h:item.time.h}}</p>
                                        <p>:</p>
                                        <p>{{item.time.m < 10 ? '0' + item.time.m:item.time.m}}</p>
                                        <p>&nbsp;-&nbsp;</p>
                                        <p>{{item.time.day < 10 ? '0' + item.time.day:item.time.day}}</p>
                                        <p>&nbsp;tháng&nbsp;</p>
                                        <p>{{item.time.month < 10 ? '0' + item.time.month:item.time.month}}</p>
                                        <p v-if="!year == item.time.year">&nbsp;-&nbsp;</p>
                                        <p v-if="!year == item.time.year">{{item.time.year}}</p>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
		</div>
        <Edit v-if="show_edit"/>
	</div>
    <Footer/>
</template>

<script>
import Nav from '../components/nav'
import Footer from '../components/footer'
import Edit from '../components/profile/edit'
import axios from 'axios'

export default {
	name: 'MainProfile',
    created(){
        this.getWatched()
    },
    methods: {
        async getWatched(){
            try {
                const response = await axios.get('/api/watched', {params: {email: this.$store.state.user.email}})
                if(response.data.success){
                    this.watcheds = response.data.data
                }
            } catch(err){
                console.log(err)
            }
            this.show_history = false
        }
    },
	beforeMount() {
        window.scrollTo(0,0)
		document.title = this.$store.state.user.name + ' | Hồ sơ'
        document.body.style.overflowY = 'scroll'
	},
    data(){
        return {
            show_edit: false,
            watcheds: [],
            show_history: true
        }
    },
    setup(){
        return {
            months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
            year: new Date().getFullYear()
        }
    },
	components: {
		Nav,
        Edit,
		Footer
	},
    watch: {
        show_edit: function(){
            if(this.show_edit){
                document.body.style.overflow = 'hidden'
            } else {
                document.body.style.overflowY = 'scroll'
            }
        }
    }
}
</script>

