<template>
	<nav>
        <div class="menu">
            <div class="menu-left" @click="() => this.$router.push({name: 'Home'})">
                <img src="../../assets/logo.jpg" class="img-logo">
                <h3>TE</h3>
                <h3>Phim</h3>
            </div>
            <div class="menu-right" :class="{menu_right_show: show_menu_right, menu_right_scroll: show_menu_category || show_menu_country}">
                <ul>
                    <li class="li-arrow-right"><i class="fa-solid fa-arrow-right-long" @click="() => show_menu_right=!show_menu_right"></i></li>
                    <li class="li-user" v-if="this.$store.state.auth">
                        <div class="div-li-user">
                            <img :src="this.$store.state.user.avata.url" class="li-img-avata">
                            <h3 @click="() => this.$router.push({name: 'Profile'})">{{this.$store.state.user.name}}</h3>
                        </div>
                    </li>
                    <li @click="() => this.$router.push({name: 'List', params: {type: 'phim-bo'}})" :class="{li: this.$route.params.type === 'phim-bo'}"><p>Phim Bộ</p></li>
                    <li @click="() => this.$router.push({name: 'List', params: {type: 'phim-le'}})" :class="{li: this.$route.params.type === 'phim-le'}"><p>Phim Lẻ</p></li>
                    <li @click="() => this.$router.push({name: 'List', params: {type: 'shows'}})" :class="{li: this.$route.params.type === 'shows'}"><p>Shows</p></li>
                    <li @click="() => this.$router.push({name: 'List', params: {type: 'hoat-hinh'}})" :class="{li: this.$route.params.type === 'hoat-hinh'}"><p>Hoạt Hình</p></li>
                    <li @click.prevent="() => show_menu_category=!show_menu_category" :class="{li: cate == 1}" class="li-category-parent">
                        <div class="menu-category"><p>Thể Loại<i class="fa-solid fa-caret-down" v-if="!show_menu_category"></i><i class="fa-solid fa-xmark" v-else="show_menu_category"></i></p></div>
                        <div class="bg-category" @click.stop="() => show_menu_category=false" v-if="show_menu_category"></div>
                        <div class="nav-category" :class="{nav_category_open: show_menu_category}">
                            <div class="ul-category">
                                <ul>
                                    <li v-for="(text, i) in category" :key="i" @click.stop="selectCategory(text)" :class="{li_: returnSlug(text) === this.$route.params.category}"><p>{{text}}</p></li>
                                </ul>
                            </div>
                        </div>
                    </li>
                    <li @click.prevent="() => show_menu_country= !show_menu_country" :class="{li: cou == 1}" class="li-country-parent">
                        <div class="menu-country"><p>Quốc Gia<i class="fa-solid fa-caret-down" v-if="!show_menu_country"></i><i class="fa-solid fa-xmark" v-else="show_menu_country"></i></p></div>
                        <div class="bg-country" @click.stop="() => show_menu_country=false" v-if="show_menu_country"></div>
                        <div class="nav-country" :class="{nav_country_open: show_menu_country}">
                            <div class="ul-country">
                                <ul>
                                    <li v-for="(text, i) in country" :key="i" @click.stop="selectCountry(text)" :class="{li_: returnSlug(text) === this.$route.params.country}"><p>{{text}}</p></li>
                                </ul>
                            </div>
                        </div>
                    </li>
                    <li class="li-btn-login-or-register" v-if="!this.$store.state.auth">
                        <div class="btn-lg-sm">
                            <button @click="() => this.$router.push({name: 'Login'})">Đăng nhập</button>
                            <button @click="() => this.$router.push({name: 'Register'})">Đăng ký</button>
                        </div>
                    </li>
                     <li class="li-btn-logut" v-if="this.$store.state.auth">
                        <div class="btn-lg-sm">
                            <button @click="Logout()">Đăng xuất</button>
                        </div>
                    </li>
                </ul>
            </div>
            <i class="fa-solid fa-bars" @click="() => show_menu_right = !show_menu_right"></i>
        </div>
    </nav>
    <div class="box-search-account" :class="{box_search_sm: this.$route.name === 'Movie' || this.$route.name === 'Play' || this.$route.name === 'Category' || this.$route.name === 'Country' || this.$route.name === 'List' || this.$route.name === 'Search'}">
        <div class="search">
            <input type="text" placeholder="Tên phim" v-model="search" @keyup.enter.native="onSearch()">
            <i class="fa-solid fa-magnifying-glass" @click="onSearch()"></i>
        </div>
         <div class="btn" v-if="!this.$store.state.auth">
            <button @click="() => this.$router.push({name: 'Login'})">Đăng nhập</button>
            <button @click="() => this.$router.push({name: 'Register'})">Đăng ký</button>
        </div>
        <div class="box-user" v-if="this.$store.state.auth">
