            <div class="bg-big-user" v-show="show_user" @click="() => show_user = false"></div>
            <div class="user" v-show="show_user">
                <div class="name"><h4 @click="() => this.$router.push({name: 'Profile'})">{{this.$store.state.user.name}}</h4></div>
                <button @click="Logout()">Đăng xuất</button>
            </div>
        </div>
    </div>
</template>

<script>
import slug from 'slug'

export default {
	name: 'Nav',
    props: ['cate', 'cou'],
    data(){
        return {
            show_menu_category: false,
            show_menu_country: false,
            show_user: false,
            show_menu_right: false,
            category: ['Hành Động',
                        'Tình Cảm',
                        'Hài Hước',
                        'Cổ Trang',
                        'Tâm Lý',
                        'Hình Sự',
                        'Chiến Tranh',
                        'Thể Thao',
                        'Võ Thuật',
                        'Viễn Tưởng',
                        'Phiêu Lưu',
                        'Khoa Học',
                        'Kinh Dị',
                        'Âm Nhạc',
                        'Thần Thoại',
                        'Tài Liệu',
                        'Gia Đình',
                        'Chính kịch',
                        'Bí ẩn',
                        'Học Đường',
                        'Kinh Điển'],
            country: ['Trung Quốc',
                        'Hàn Quốc',
                        'Nhật Bản',
                        'Thái Lan',
                        'Âu Mỹ',
                        'Đài Loan',
                        'Hồng Kông',
                        'Ấn Độ',
                        'Anh',
                        'Pháp',
                        'Canada',
                        'Quốc Gia Khác',
                        'Đức',
                        'Tây Ban Nha',
                        'Thổ Nhĩ Kỳ',
                        'Hà Lan',
                        'Indonesia',
                        'Nga',
                        'Mexico',
                        'Ba lan',
                        'Úc',
                        'Thụy Điển',
                        'Malaysia',
                        'Brazil',
                        'Philippines',
                        'Bồ Đào Nha',
                        'Ý',
                        'Đan Mạch',
                        'UAE',
                        'Na Uy',
                        'Thụy Sĩ',
                        'Châu Phi',
                        'Nam Phi',
                        'Ukraina',
                        'Ả Rập Xê Út'],
            search: ''
        }
    },
    methods: {
        selectCategory(text){
            text = slug(text)
            this.show_menu_category = false
            this.$router.push({name: 'Category', params: { category: text }})
        },
        selectCountry(text){
            text = slug(text)
            this.show_menu_country = false
            this.$router.push({name: 'Country', params: { country: text }})
        },
        onSearch(){
            if(this.search){
                this.$router.push({name: 'Search', params: { search: this.search }})
            }
        },
        returnSlug(text){
            return slug(text)
        },
        Logout(){
            localStorage.removeItem('token')
            location.reload('/')
        }
    },
    created(){
        if(this.$route.params.search){
            this.search = this.$route.params.search
        }
    },
    watch: {
        show_menu_category: function(){
            if(this.show_menu_category){
                document.body.style.overflow = 'hidden'
            } else {
                document.body.style.overflowY = 'scroll'
            }
        },
        show_menu_country: function(){
            if(this.show_menu_country){
                document.body.style.overflow = 'hidden'
            } else {
                document.body.style.overflowY = 'scroll'
            }
        },
        show_menu_right: function(){
             if(this.show_menu_right){
                document.body.style.overflow = 'hidden'
            } else {
                document.body.style.overflowY = 'scroll'
            }
        },
        '$route.path': function(){
            this.show_menu_right = false
        }
    }
}
</script>

