import { createRouter, createWebHistory } from "vue-router"

import Home from '@/views/home'
import Movie from '@/views/movie'
import Play from '@/views/play'
import List from '@/views/list'
import Category from '@/views/category'
import Country from '@/views/country'
import Search from '@/views/search'
import Register from '@/views/register'
import Login from '@/views/login'
import Profile from '@/views/profile'
import PageNotFound from '@/views/page-not-found'

import axios from 'axios'
import store from '../store'

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/phim/:slug',
        name: 'Movie',
        component: Movie
    },
    {
        path: '/play/:slug',
        name: 'Play',
        component: Play
    },
    {
        path: '/danh-sach/:type',
        name: 'List',
        component: List
    },
    {
        path: '/the-loai/:category',
        name: 'Category',
        component: Category
    },
    {
        path: '/quoc-gia/:country',
        name: 'Country',
        component: Country
    },
    {
        path: '/tim-kiem/:search',
        name: 'Search',
        component: Search
    },
    {
        path: '/dang-ky',
        name: 'Register',
        component: Register
    },
    {
        path: '/dang-nhap',
        name: 'Login',
        component: Login
    },
    {
        path: '/ho-so',
        name: 'Profile',
        component: Profile
    },
    {
        path: '/:pathMatch(.*)*',
        name: 'PageNotFound',
        component: PageNotFound
    }
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

router.beforeEach(async (to, from, next) => {
    const token = localStorage.getItem('token') || ''
    try {
        if(token){
            const response = await axios.get('/api/login/' + token)
            if(response.data.success){
                store.dispatch( 'updateUser', response.data.data) 
            }
        }
    } catch(err){
        console.log(err)
    }
    if(!store.state.auth && to.name == 'Profile'){
        next('/')
    }
    if(store.state.auth && (to.name == 'Login' || to.name == 'Register')){
        next('/')
    }
    next()
})

export default router