<template>
	<div class="row">
        <div class="row-left">
            <div class="title-left">
                <h1>Phim hoạt hình</h1>
            </div>
            <div class="loading" v-if="load">
                <div class="load"><h4>TE</h4><h4>Phim</h4></div>
            </div>
            <Item
                v-for="(item, i) in list"
                :key="i"
                :item="item"
            />
        </div>
        <div class="row-right">
            <div class="title-right">
                <h2>Phim hoạt hình hot</h2>
            </div>
            <div class="row-right-child">
                <ItemHot
                    v-for="(item, i) in list_hot"
                    :key="i"
                    :item="item"
                />
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Item from './item'
import ItemHot from './item-hot'

export default {
	name: 'NewMovies',
    data(){
        return {
            list: [],
            list_hot: [],
            load: true
        }
    },
    created(){
        this.getNewAnime()
        this.getNewAnimeHot()
    },
    methods: {
        async getNewAnime(){
            try {
                const result = await axios.get('/api/phimhoathinhmoi')
                if(result.data.success){
                    this.list = result.data.data
                }
            }catch(err){
                console.log(err)
            }
            this.load = false
        },
        async getNewAnimeHot(){
            try {
                const result = await axios.get('/api/phimhoathinhmoihot')
                if(result.data.success){
                    this.list_hot = result.data.data
                }
            }catch(err){
                console.log(err)
            }
        }
    },
    components: {
        Item,
        ItemHot
    }
}
</script>

